
:root {
    --card-height: 350px;
    --card-width: calc(var(--card-height) / 1.5);
  }
  *{
    box-sizing: border-box;
  }
  .car {
    width: var(--card-width);
    height: var(--card-height);
    position: relative;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    padding: 0 36px;
    perspective: 2500px;
    margin: 0 50px;
    margin-top: 185px;
  }
  .cover-image{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 20px;
  }
  .wrapper{
    transition: all 0.5s;
    position: absolute;
    width: 100%;
    z-index: -1;
  }
  .car:hover .wrapper{
    transform: perspective(900px) translateY(-5%) rotateX(25deg) translateZ(0);
    box-shadow: 2px 35px 32px -8px rgba(0, 0, 0, 0.75);
    -webkit-box-shadow:2px 35px 32px -8px rgba(0, 0, 0, 0.75);
    -moz-box-shadow:2px 35px 32px -8px rgba(0, 0, 0, 0.75);
  }
  .wrapper::before,
  .wrapper::after{
    content: "";
    opacity: 0;
    width: 100%;
    height: 80px;
    transition: all 0.5s;
    position: absolute;
    left: 0;
  }
  .wrapper::before{
    top: 0;
    height: 100%;
    background-image: linear-gradient(
        to top,
        transparent 46%,
        rgba(12, 13, 19, 0.5)68%,
        rgba(12, 13, 19) 97%
    );
  }
  .wrapper::after{
    bottom: 0;
    opacity: 1;
    background-image: linear-gradient(
        to bottom,
        transparent 46%,
        rgba(12, 13, 19, 0.5) 68%,
        rgba(12, 13, 19)97%
    );
  }
  .car:hover .wrapper::before,
  .wrapper::after{
    opacity: 1;
  }
  .car:hover .wrapper::after{
    height: 120px;
  }
  .title {
    width: 100%;
    transition: transform 0.5s;
  }
  .car:hover .title {
    transform: translate3d(0%, -50px, 100px);
  }
  .character {
    width: 100%;
    opacity: 0;
    transition: all 0.5s;
    position: absolute;
    z-index: -1;
  }
  .car:hover .character {
    opacity: 1;
    transform: translate3d(0%, -30%, 100px);
  }
  .covers-image{
    margin-bottom: 20px;
  }
  @media (max-width: 768px){
    .car{
        margin-top: 0;
        margin-left: 0;
    }
  }

